//////////////////////////////////////////////////////////// MAIN APP INTERFACE
.app {
  @include flex-center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  min-height: 100vh;
  background-color: $cold-dark-blue;
  padding: 1rem;

  &__header {
    @include flex-center;
    position: fixed;
    top: 0;
    right: 0;
    height: $layout-xs-header-height;
    width: 100%;
    z-index: 1;
    background-color: $cold-dark-blue;
    padding: 0 $layout-xs-sm-padding;
  }

  &__container {
    @include flex-center;
    width: 60%;
    height: 100%;
    max-width: 1200px;
    margin-top: 4rem;
    flex-direction: column;
  }

  &__button {
    border-radius: 4px;
    padding: 12px;
    min-width: 200px;
    color: $cold-light-blue;
    font-size: 0.875rem;
    transition: box-shadow $time-hover-effect, background-color $time-hover-effect;

    &--add {
      font-size: 1.125rem;
      font-weight: bold;
      background-color: $cold-blue;
      color: $cold-darkest-blue;
      margin-bottom: 2rem;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      

      &:hover {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
      }
    }

    &--remove {
      margin-bottom: 1rem;

      &:hover {
        background-color: $cold-blue;
      }
    }
  }
}
//////////////////////////////////////////////////////////////////// NAVIGATION
.navigation {
  flex-grow: 1;

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  &__item {
  }

  &__button {
    color: $cold-light-blue;
    padding: 0.5rem;
    border: 2px solid transparent;

    &:hover {
      border-bottom-color: $cold-light-blue;
    }
  }
}
////////////////////////////////////////////////////////////////////////// LOGO
.logo {
  flex-grow: 1;

  &__link {
    @include flex-center;
    position: relative;
    max-width: 120px;
  }

  &__svg {
    fill: $cold-blue;
    width: 100%;
    height: 100%;

    &--color {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      fill: $cold-light-blue;
    }
  }
}
///////////////////////////////////////////////////////////////////////// GLASS
.glass {
  @include flex-center;
  position: relative;

  &__svg {
    width: 100%;
    max-height: 400px;
    margin-bottom: 48px;
  }

  &__fill {
    fill: $cold-blue;
  }

  &__outline {
    fill: none;
    stroke: $cold-light-blue;
    stroke-width: 10px;
  }

  &__counter {
    margin: 0;
    text-align: center;
    font-size: 7rem;
    position: absolute;
  }
}